import React, { Component } from "react";
import { Link } from "react-router-dom";

class LatestNewsArticle extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoaded: false,
      articles: []
    };
  }

  componentDidMount() {
    this.fetchData();
  }

  fetchData() {
    fetch(
      "https://raw.githubusercontent.com/Devon323/minibus-taxi-industry-sa-newsfeed/master/rank-finder-newsfeed.json"
    )
      .then(response => response.json())
      .then(parsedJSON => {
        console.log(parsedJSON);
        this.setState({
          isLoaded: true,
          articles: parsedJSON.articles
        });
      });
  }

  render() {
    const { error, isLoaded, articles } = this.state;

    if (error) {
      return <div>Error: {error.message}</div>;
    } else if (!isLoaded) {
      return <div>Loading...</div>;
    } else {
      return (
        <div
          className="jumbotron jumbotron-fluid mb-0 overlay text-white"
          style={{
            position: `relative`,
            height: "640px",
            backgroundImage: `url( ${articles[0].urlToImage} )`,
            backgroundRepeat: `no-repeat`,
            backgroundSize: `cover`,
            zIndex: `1`
          }}
        >
          <div
            style={{
              position: `absolute`,
              backgroundColor: `#1c3e59`,
              height: `674px`,
              width: `100%`,
              top: `inherit`,
              left: `inherit`,
              opacity: `0.8`,
              marginTop: `-64px`,
              zIndex: `-1`
            }}
          />
          <div className="container">
            <h1
              className="display-4 px-3"
              style={{
                width: `fit-content`,
                background: `rgba(0,0,0, 0.5)`
              }}
            >
              Latest News
            </h1>
            <p
              className="lead p-2 m-0"
              style={{
                textDecorationLine: `underline`,

                background: `rgba(0,0,0, 0.5)`
              }}
            >
              {articles[0].title}
            </p>
            <p
              className="card-text p-2"
              style={{
                background: `rgba(0,0,0, 0.5)`
              }}
            >
              {articles[0].description}
            </p>
            <a href={articles[0].url} className="btn button" target="_blank">
              Read More
            </a>{" "}
            <Link to="/newsfeed" className="btn button" target="_blank">
              See All Articles
            </Link>
          </div>
        </div>
      );
    }
  }
}

export default LatestNewsArticle;
