import React, { Component } from "react";
import Home from "./home";

let contacts = [
  {
    id: 1,
    name: "Retreat Taxi Rank",
    price: "R10",
    keywords: "Retreat - Wynberg"
  },
  {
    id: 2,
    name: "Courtney",
    price: "R10",
    keywords: "Steenberg"
  },
  {
    id: 3,
    name: "Tim",
    price: "R10",
    keywords: "Muizenberg"
  },
  {
    id: 4,
    name: "Jeff",
    price: "R10",
    keywords: "Cape Town"
  }
];
/*
- Create JSON Object for all the taxis and it's destinations
- If a user "retreat" it should show all the taxis that travel to and from retreat taxi rank
- Initially, all taxi ranks will be diplayed and then narrowed down in the search  

*/
function searchingFor(term) {
  return function(x) {
    return x.keywords.toLowerCase().includes(term.toLowerCase()) || !term;
  };
}

class Results extends Component {
  constructor(props) {
    super(props);
    this.state = {
      contacts: contacts,
      term: ""
    };

    this.searchHandler = this.searchHandler.bind(this);
  }

  searchHandler(event) {
    console.log(event.target.value);
    this.setState({ term: event.target.value });
  }

  render() {
    const { term, contacts } = this.state;
    return (
      <div>
        <ul>
          {contacts.filter(searchingFor(term)).map(contact => (
            <div key={contact.id}>
              <li>
                {contact.name} - {contact.price}
              </li>
            </div>
          ))}
        </ul>{" "}
        {/* <ul>
        //   {this.state.contacts
        //     .filter(searchingFor(this.state.term))
        //     .map(contact => {
        //       return <li key={contact.id}> {contact} <li/>;
        //   })}
        // </ul> */}
        <form>
          <div className="form-row">
            <div className="col-12 col-md-9 mb-2 mb-md-0">
              <input
                type="text"
                className="form-control form-control-lg"
                placeholder="Enter destination..."
                value={term}
                onChange={this.searchHandler}
              />
            </div>
            <div className="col-12 col-md-3">
              <button type="submit" className="btn btn-block btn-lg">
                Go
              </button>
            </div>
          </div>
        </form>
      </div>
    );
  }
}

export default Results;
