import React, { Component } from "react";
import { Link } from "react-router-dom";
import "../../App.css";
import "../../css/simple-line-icons.css";
import ReactDisqusComments from "react-disqus-comments";
import Results from "./results";
import LatestNewsArticle from "../layouts/latestNewsArticle";

class Home extends Component {
  // state = {
  //   search: ""
  // };
  handleNewComment = comment => {
    console.log(comment.text);
  };

  render() {
    return (
      <React.Fragment>
        <header className="masthead text-white text-center">
          <div className="overlay" />
          <div className="container" style={{ diplay: "none" }}>
            <div className="row">
              <div className="col-xl-9 mx-auto">
                <h1 className="mb-5">Type in a destination</h1>
              </div>
              <div className="col-md-10 col-lg-8 col-xl-7 mx-auto">
                <form>
                  <div className="form-row">
                    <div className="col-12 col-md-9 mb-2 mb-md-0">
                      <input
                        type="text"
                        className="form-control form-control-lg"
                        placeholder="Enter destination..."
                        // value={this.state.term}
                        // onChange={this.searchHandler}
                      />
                    </div>
                    <div className="col-12 col-md-3">
                      <button type="submit" className="btn btn-block btn-lg">
                        Go
                      </button>
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </header>

        {/* <!-- This snippet uses Font Awesome 5 Free as a dependency. You can download it at fontawesome.io! --> */}
        <LatestNewsArticle />
        <div className="container py-5">
          <div className="row">
            {/* Tips Snippet*/}
            <div className="col-lg-12">
              <div className="card mb-12 mb-lg-0">
                <div className="card-body card-height">
                  <h5 className="card-title text-muted text-uppercase text-center">
                    Tips
                  </h5>

                  <hr />
                  <p>Some things to keep in mind when commuting by taxi.</p>
                  <Link to="/tips" className="btn btn-block button">
                    Read More
                  </Link>
                </div>
              </div>
            </div>
            {/* End Tips Snippet*/}
          </div>
        </div>

        <div className="container text-center ">
          <hr className="bg-primary" />
        </div>

        {/* Disqus */}
        <div className="container mb-3">
          <div className="row">
            <div className="col-lg-12">
              <ReactDisqusComments
                shortname="randfinder"
                identifier="something-unique-randfinder"
                title="Example Thread"
                url="http://www.example.com/example-thread"
                category_id="1215113"
                onNewComment={this.handleNewComment}
              />
            </div>
          </div>
        </div>

        <div className="container">
          <hr className="bg-primary" />
        </div>
      </React.Fragment>
    );
  }
}

export default Home;
