import React, { Component } from "react";

class NewsFeed extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoaded: false,
      articles: []
    };
  }

  //Client key ID - 639c8ec2-dbc7-4c19-b078-fa83a44aacfe
  //Client key secret - HUD9cGNjjFX7bbLVH245UG9TJdasbB2BpB+TdVlo4fM=

  // https://feed2json.org/convert?url=http%3A%2F%2Ffetchrss.com%2Frss%2F5ce0627c8a93f837408b45675ce062128a93f8523a8b4567.xml - Converted from (https://www.news24.com/Tags/Topics/taxi) to RSS feed in (https://fetchrss.com/) then convert that link to JSON Object

  //"https://newsapi.org/v2/top-headlines?country=za&apiKey=ef67bbc888aa460fad925c0383d2ff0b"

  componentDidMount() {
    this.fetchData();
  }

  fetchData() {
    fetch(
      "https://raw.githubusercontent.com/Devon323/minibus-taxi-industry-sa-newsfeed/master/rank-finder-newsfeed.json"
    )
      .then(response => response.json())
      .then(parsedJSON => {
        console.log(parsedJSON);
        this.setState({
          isLoaded: true,
          articles: parsedJSON.articles
        });
      });
    // fetch("http://feeds.news24.com/articles/news24/MyNews24/rss")
    //   .then(response => response.text())
    //   .then(str => new window.DOMParser().parseFromString(str, "text/xml"))
    //   .then(data => {
    //     for (var i = 0; i < data.getElementsByTagName("item").length; i++) {
    //       for (var x = 0; x < data.getElementsByTagName("title").length; x++)
    //         console.log(
    //           data.getElementsByTagName("title")[x + 1].childNodes[0]
    //             .nodeValue +
    //             "\n" +
    //             data.getElementsByTagName("description")[x].childNodes[0]
    //               .nodeValue
    //         );
    //     }
    //   })
    //   .catch(error => console.log("parsing failed", error));
  }

  render() {
    const { error, isLoaded, articles } = this.state;
    if (error) {
      return <div>Error: {error.message}</div>;
    } else if (!isLoaded) {
      return <div className="d-flex justify-content-center">Loading...</div>;
    } else {
      return (
        <div className="container mt-5">
          <div className="card-columns">
            {articles.map(article => (
              <div key={article.title} className="card">
                <img
                  className="card-img-top img-fluid"
                  src={article.urlToImage}
                  alt={article.title}
                />
                <div className="card-body">
                  <h4 className="card-title">
                    {article.title}{" "}
                    <small className="small"> | {article.publishedAt} </small>
                  </h4>
                  <p className="card-text">{article.description}</p>

                  <a href={article.url} className="btn button">
                    Read More
                  </a>
                </div>
              </div>
            ))}
          </div>
        </div>
      );
    }
  }
}

export default NewsFeed;
