import React, { Component } from "react";

function Contact() {
  return (
    <div className="container-fluid">
      <div className="row no-gutter">
        <div className="d-none d-md-flex col-md-4 col-lg-5 bg-image" />
        <div className="col-md-8 col-lg-7">
          <div className="container mb-5 mt-5">
            <h3 className="login-heading ">Contact Us</h3>
            <form
              method="GET"
              action="mailto:rankfindercpt@gmail.com"
              enctype="text/plain"
            >
              <div className="form-group mt-3">
                <div className="input-group mb-2">
                  <div className="input-group-prepend">
                    <div className="input-group-text">
                      <i className="icon icon-user text-info" />
                    </div>
                  </div>
                  <input
                    type="text"
                    className="form-control"
                    id="username"
                    name="username"
                    placeholder="Enter name here..."
                    required
                  />
                </div>
              </div>
              <div className="form-group">
                <div className="input-group mb-2">
                  <div className="input-group-prepend">
                    <div className="input-group-text">
                      <i className="icon icon-envelope text-info" />
                    </div>
                  </div>
                  <input
                    type="email"
                    className="form-control"
                    id="email"
                    name="email"
                    placeholder="Enter email here..."
                    required
                  />
                </div>
              </div>

              <div className="form-group">
                <div className="input-group mb-2">
                  <div className="input-group-prepend">
                    <div className="input-group-text">
                      <i className="icon icon-pencil text-info" />
                    </div>
                  </div>
                  <textarea
                    className="form-control"
                    placeholder="Type message here..."
                    required
                  />
                </div>
              </div>

              <div className="text-center">
                <input
                  type="submit"
                  value="Send"
                  className="btn btn-info btn-block rounded-0 py-2"
                />
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Contact;
