import React from "react";
import "../../App.css";

function Footer() {
  const footerPos = {
    bottom: "0"
  };
  return (
    <React.Fragment>
      {/* <!-- Footer --> */}
      <footer
        className="page-footer font-small footer-body text-light pt-4 footerPos"
        style={footerPos}
      >
        <div className="container">
          {/* <!-- Footer Links --> */}
          <div className="container-fluid text-center text-md-left">
            {/* <!-- Grid row --> */}
            <div className="row">
              {/* <!-- Grid column --> */}
              <div className="col-md-6 mt-md-0 mt-3">
                {/* <!-- Content --> */}
                <h5 className="text-uppercase">Footer Content</h5>
                <p>References</p>
              </div>
              {/* <!-- Grid column --> */}

              <hr className="clearfix w-100 d-md-none pb-3" />

              {/* <!-- Grid column --> */}
              <div className="col-md-3 mb-md-0 mb-3">
                {/* <!-- Links --> */}
                <h5 className="text-uppercase">Contact</h5>
                <p className="small">Email: rankfindercpt@gmail.com</p>
                <p className="small">Tel: 021 1203 5962</p>
              </div>
              {/* <!-- Grid column --> */}
            </div>
            {/* <!-- Grid row --> */}
          </div>
          {/* <!-- Footer Links --> */}
        </div>
        {/* <!-- Copyright --> */}
        <div className="footer-copyright text-center py-3">
          Copyright ©2019 | Rank Finder
        </div>
        {/* <!-- Copyright --> */}
      </footer>
      {/* <!-- Footer --> */}
    </React.Fragment>
  );
}

export default Footer;
