import React, { Component } from "react";
import "../../css/tips.css";
import "../../css/simple-line-icons.css";

function Tips() {
  const removeMargin = {
    marginRight: "0",
    marginLeft: "0"
  };
  return (
    <React.Fragment>
      <div className="jumbotron jumbotron-fluid mb-0">
        <div className="container">
          <h1 className="display-4">New to taxi commuting?</h1>
          <p className="lead">
            Here are some things to keep in mind when commuting by taxi.
          </p>
        </div>
      </div>

      {/* Tips template */}

      <div className="row text-light" style={removeMargin}>
        <div className="d-none d-lg-block col-lg-3 col-md-12 col-sm-12 img-placeholder">
          <span className="icon icon-check icon-style" />
          {/* icon placeholder */}
        </div>
        <div className="col-lg-9 col-md-12 col-sm-12 pt-3 bg-dark">
          <div className="card-body">
            <h5 className="card-title">How to Get a Taxi</h5>
            <p className="card-text">
              If you're waiting on the side off the road simply raise your hand.
            </p>
            <p className="card-text">
              Safety tip: Do not get into an empty taxi especially if there is
              no destination card located on the window screen of the taxi.
            </p>
          </div>
        </div>
      </div>

      <hr className="d-lg-none d-xl-none bg-light m-0" />

      {/* Tips template */}
      <div className="row text-light" style={removeMargin}>
        <div className="col-lg-9 col-md-12 col-sm-12 pt-3 bg-dark">
          <div className="card-body">
            <h5 className="card-title">Stay Covered</h5>
            <p className="card-text">
              Always make sure you have the EXACT amount of money before
              stepping into a taxi. The 'gaatjies' or sliding-door-operator (you
              won't miss them they're usually the loudest people in Cape Town)
              will collect money your money.
            </p>
          </div>
        </div>
        <div className="d-none d-lg-block col-lg-3 col-md-12 col-sm-12 img-placeholder">
          <span className="icon icon-wallet icon-style" />
          {/* icon placeholder */}
        </div>
      </div>

      <hr className="d-lg-none d-xl-none bg-light m-0" />

      {/* Tips template */}
      <div className="row text-light" style={removeMargin}>
        <div className="d-none d-lg-block col-lg-3 col-md-12 col-sm-12 img-placeholder">
          <span className="icon icon-eye icon-style" />
          {/* icon placeholder */}
        </div>
        <div className="col-lg-9 col-md-12 col-sm-12 pt-3 bg-dark">
          <div className="card-body">
            <h5 className="card-title">Be Visualant</h5>
            <p className="card-text">
              If you are travelling in the evening take note of taxi you're
              getting into. Memorise the number plate, let somewhere know as a
              precausion. Again, do not get into an empty taxi.
            </p>
            <p className="card-text">
              Tip: If you are travelling at night be sure to have extra money as
              drivers may charge extra.
            </p>
          </div>
        </div>
      </div>

      <hr className="d-lg-none d-xl-none bg-light m-0" />

      {/* Tips template */}
      <div className="row text-light" style={removeMargin}>
        <div className="col-lg-9 col-md-12 col-sm-12 pt-3 bg-dark">
          <div className="card-body">
            <h5 className="card-title">Comfirm Your Destination</h5>
            <p className="card-text">
              When commuting tell the gaatjie or driver your destination and you
              close remind them again.
            </p>
          </div>
        </div>
        <div className="d-none d-lg-block col-lg-3 col-md-12 col-sm-12 img-placeholder">
          <span className="icon icon-pin icon-style" />
          {/* icon placeholder */}
        </div>
      </div>
    </React.Fragment>
  );
}

export default Tips;
