import React from "react";
import "../../App.css";

function About() {
  return (
    <React.Fragment>
      <div className="jumbotron jumbotron-fluid mb-0">
        <div className="container">
          <h1 className="display-4">About</h1>
          <p className="lead">A little it about us.</p>
        </div>
      </div>

      <div className="container-fluid bg-dark text-light">
        <div className="row no-gutter">
          <div className="d-none d-md-flex col-md-4 col-lg-4 bg-image" />
          <div className="col-md-8 col-lg-6">
            <div className="login d-flex align-items-center py-5">
              <div className="container">
                <div className="row">
                  <div className="col-md-9 col-lg-8 mx-auto">
                    <h1 className="pb-3">Why do we exist?</h1>
                    <p>
                      The web application servers to address the social issue
                      with regards to the lack of information in the taxi
                      industry.
                    </p>
                    <p>
                      Taxis are the most reliable upon mode of public transport
                      in South Africa and specifically Cape Town. The problem
                      lies with the lack of information within the taxi industry
                      and this website serves as a digital platform to
                      centralise information so that it may be accessible to the
                      public.
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
}

export default About;
