import React, { Component } from "react";
import axios from "axios";

function searchingFor(query) {
  return function(x) {
    return x.title.toLowerCase().includes(query.toLowerCase()) || !query;
  };
}

class Search extends Component {
  //Making Props available in Constructor
  constructor(props) {
    super(props);
    this.state = {
      //Storing user's query results
      query: "",
      //Storing results receieved from the API
      results: "",
      //Show loading while the results is being fetched
      loading: false,
      //To display errors or if no data is available
      message: ""
    };
    this.cancel = "";
  }

  fetchSearchResults = query => {
    const searchUrl =
      "https://raw.githubusercontent.com/Devon323/minibus-taxi-industry-sa-newsfeed/master/rank-finder-newsfeed.json";
    if (this.cancel) {
      this.cancel.cancel();
    }
    this.cancel = axios.CancelToken.source();

    axios
      .get(searchUrl, {
        cancelToken: this.cancel.token
      })
      .then(res => {
        const resultNotFoundMgs = !res.data.articles.length
          ? "There are no more search results. Please try a new search"
          : "";
        this.setState({
          results: res.data.articles,
          message: resultNotFoundMgs,
          loading: false
        });
        console.warn(res.data);
      })
      .catch(error => {
        if (axios.isCancel(error) || error) {
          this.setState({
            loading: false,
            message: "Failed to fetch the data. Please check network"
          });
        }
      });
  };

  //Handles an event when user inputs text - get the string the users types in
  handleOnInputChange = event => {
    //Stores user string
    const query = event.target.value;
    // console.log(query);

    //When the user hits backspace the results disappear
    if (!query) {
      this.setState({ query, results: {}, message: "" });
    } else {
      //Set input information to the state
      this.setState({ query: query, loading: true, message: "" }, () => {
        this.fetchSearchResults(query);
      });
    }
  };

  renderSearchResults = () => {
    const { results, query } = this.state;
    if (Object.keys(results).length && results.length) {
      return (
        <div className="container">
          {results.filter(searchingFor(query)).map(result => {
            return (
              <div key={result.title}>
                <li>{result.title}</li>
              </div>
            );
          })}
        </div>
      );
    }
  };

  render() {
    const { query } = this.state;
    // console.warn(this.state);
    return (
      <div className="container">
        <h2> Live Search: React Application</h2>
        <form>
          <div className="form-row">
            <div className="col-12 col-md-9 mb-2 mb-md-0">
              <input
                name={query}
                type="text"
                className="form-control form-control-lg"
                placeholder="Enter destination..."
                onChange={this.handleOnInputChange}
              />
            </div>
          </div>
        </form>
        {/* Error Message */}
        {this.state.message && <p>{this.state.message} </p>}

        {/* Results */}
        {this.renderSearchResults()}
      </div>
    );
  }
}

export default Search;
